.sidebar {
    background-color: indigo;

    h2 {
        color: white;
        margin: 0;
        text-align: center;
        padding: 2rem;
    }

    .sidebar-nav {
        margin-top: 5rem;
        a {
            color: white;
            display: block;
            margin-bottom: 1rem;
            padding: .6rem;
            transition-property: background-color;
            transition-duration: 300ms;
            text-transform: uppercase;            


            &:hover,
            &.activo {
                background-color: darken(indigo, 20%);
                border-radius: .5rem;
            }
        }
    }
}